.home-page {
  position: relative;

  position: relative;

  &-login {
    z-index: 2;
    color: whitesmoke;
    background: #00000099;
    position: fixed;
    width: 100%;
    top: 0px;
    right: 0px;
    padding: 15px 20px;

    .MuiButtonBase-root {
      font-size: rem(16px);
      width: auto;
      color: rgba(250, 250, 250, 0.932);
      transition: 0.3s;
      margin-left: 5px;

      .MuiButton-outlined {
        padding: 15px 25px;
      }

      &:hover {
        background: rgba(255, 255, 255, 0.185);
      }
    }
  }

  h2 {
    margin: 0px 15px;
    font-size: 2.2rem;
    color: white;
  }

  //header -----------------------

  &-header-container {
    background: #00000099;
    height: 85vh;

    .header-image {
      width: 100%;
      height: 100%;
      background-color: #00000099;
      background-image: url(../images/header-bg.jpg);
      background-size: cover;
      background-position-x: center;
      background-blend-mode: darken;
      border-radius: 5px;
    }

    h2 {
      font-weight: 600;
      text-shadow: 1px 1px 1px black;
    }

    .login-get-started-container {
      width: 60%;
    }

    &-about-compony-container {
      &-logo {
        width: 50%;
      }

      margin-left: 20px;

      h1 {
        text-shadow: 2px 2px 4px #000000;
      }

      p {
        font-size: 1.3rem;
        font-weight: 600;
        font-family: montserratBold, "sans-sarif";
        color: white;
        text-shadow: 2px 2px 4px #000000;
      }
    }

    &-logo {
      &-img {
        background-image: url(../images/logo.png);
        background-size: contain;
        background-repeat: no-repeat;
        height: 140px;
        width: 270px;
      }
    }

    .MuiFab-root {
      color: white;
      background: #1d941ddb;
      box-shadow: rgba(255, 255, 255, 0.185);
      transition: 0.4s;
      padding: 15px 30px;
      border: none;

      p {
        font-size: 1.1rem;
        font-weight: 600;
      }

      &:hover {
        background: #1d941d;
        box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.678);
      }
    }

    .MuiFab-extended {
      height: auto;
    }

    .get-started-btn {
      padding-top: 50px;
    }
  }

  // boards list -------------------------
  &-boards-list {
    padding: 50px 150px;
    background-image: linear-gradient(white, #00000040);

    @media (max-width: 1250px) {
      padding: 50px;
    }

    @media (max-width: 1055px) {
      padding: 50px 5px;
    }

    @media (max-width: 950px) {
      padding: 20px;
    }

    .home-page-boards-list-img {
      width: 600px;
      height: 290px;
      background-image: url(../images/boardtmp.png);
      background-size: cover;
      background-position-x: center;
      background-position-y: center;
      border-radius: 4px;
    }

    &-start-new-board {
      max-width: 350px;
      padding-left: 20px;

      &-card {
        cursor: pointer;
        width: 250px;
        height: 250px;
        border: 3px dashed rgb(0, 0, 0);
        border-radius: 4px;
        margin: 40px;
        transition: 0.5s;

        &:hover {
          background-color: rgba(201, 201, 201, 0.555);
          box-shadow: 0px 0px 20px 4px rgba(255, 255, 255, 0.829);
        }

        h2 {
          font-size: 1.3rem;
          color: rgb(0, 0, 0);
        }

        small {
          color: rgb(0, 0, 0);
        }
      }

      &-right-text {
        // text-align: justify;
        font-size: 1.25rem;
        color: rgb(0, 0, 0);
        font-weight: 700;
        margin-right: 10px;
        width: 330px;
        height: 290px;
        padding-left: 20px;

        @media (max-width: 1055px) {
          font-size: 1.1rem;
        }

        @media (max-width: 950px) {
          font-size: 1.1rem;
          width: 600px;
          margin: 0;
          padding: 0;
        }
      }
    }
  }

  &-footer {
    padding-top: 50px;
    min-height: 100px;
    background: black;
    color: rgb(255, 255, 255);

    .MuiSvgIcon-root {
      cursor: pointer;
      transition: 0.2s;

      &.faceBookIcon {
        &:hover {
          color: white;
          background-color: rgb(59, 89, 152);
        }
      }

      &.linkedInIcon {
        &:hover {
          color: white;
          background-color: rgb(40, 103, 178);
        }
      }
    }

    h2 {
      padding: 10px;
      margin-left: 35px;
    }

    &-team-member-card {
      margin: 0 10px;
      padding-bottom: 50px;

      a {
        color: inherit;
      }

      &-member-img {
        height: 150px;
        width: 150px;
        border-radius: 50%;
        transition: 0.2s;

        &.vlad {
          background-image: url(../images/team/Vlad.jpg);
          background-size: contain;
          background-repeat: no-repeat;
        }

        &.margad {
          background-image: url(../images/team/Margad.jpg);
          background-size: contain;
          background-repeat: no-repeat;
        }

        &.paolo {
          background-image: url(../images/team/Paolo.jpg);
          background-size: contain;
          background-repeat: no-repeat;
        }
      }
    }
  }
}

.subtitle {
  max-width: 300px;
}
